.schedule-view-wrapper {
    .modal-bg {
        width: 600px;
        margin-left: -300px;
        margin-top: -350px;
        padding: 20px 30px 30px;
        border-radius: 10px;

        .modal-title {
            margin-bottom: 30px;
            font-size: 2rem;
            font-weight: bold;
            line-height: 1.5;
        }

        .input-form {
            display: flex;
            margin-bottom: 15px;

            input[type=text] {
                width: 100%;
                height: 45px;
                padding: 13px;
                border: none;
                border-radius: 7px;
                background-color: $color-bg-gray;
                font-size: 1.4rem;
                line-height: 1.5;
            }

            .label-title {
                flex-shrink: 0;
                flex-basis: 60px;
                margin-right: 10px;
                font-size: 1.5rem;
                line-height: 45px;
            }

            &.time-date-group {
                flex-wrap: wrap;
            }

            .date-group {
                flex-basis: calc(100% - 70px);
                padding: 10px 15px 5px;
                margin-bottom: 15px;
                background-color: $color-bg-gray;
                border-radius: 5px;

                // &:last-child {
                //     position: relative;
                //     border-radius: 0 5px 5px 0;

                //     &::before {
                //         content: "";
                //         position: absolute;
                //         left: 0;
                //         top: 15px;
                //         width: 1px;
                //         height: calc(100% - 30px);
                //         background-color: $color-border1;
                //     }
                // }

                label {
                    font-size: 1.2rem;
                    line-height: 1.5;
                    font-weight: 500;
                    color: $color-purple;
                    text-transform: capitalize;
                }
            }

            .time-group {
                display: flex;
                flex-basis: calc(100% - 70px);
                padding: 10px 15px;
                margin-left: 70px;
                background-color: $color-bg-gray;
                border-radius: 5px;

                .select-box-group {
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    width: 50%;
                    height: 30px;

                    &:first-child {
                        padding-right: 25px;

                        &::after {
                            content: "";
                            position: absolute;
                            right: 0;
                            display: block;
                            width: 1px;
                            height: 20px;
                            background-color: $color-border2;
                        }
                    }

                    &:last-child {
                        padding-left: 20px;
                        padding-right: 10px;
                    }

                    label {
                        font-size: 1.1rem;
                        line-height: 1.5;
                        font-weight: 500;
                        color: $color-purple;
                        text-transform: capitalize;
                    }

                    select {
                        width: 40px;
                        height: 100%;
                        border: none;
                        background: transparent;
                        font-size: 1.2rem;
                        line-height: 1.5;
                        outline: none;
                    }
                }
            }

            input[type=date] {
                width: 100%;
                height: 45px;
                border: none;
                background: transparent;
            }


            input[type=radio] {
                display: none;
            }

            input[type=radio] + label {
                display: block;
                width: 150px;
                height: 45px;
                padding: 0 10px;
                margin-right: 10px;
                border-radius: 5px;
                border: 1px solid $color-border2;
                font-size: 1.2rem;
                line-height: 45px;
                text-transform: capitalize;
                cursor: pointer;
                transition: background 0.3s ease;
            }

            input[type=radio] + label:last-child {
                margin-right: 0;
            }

            input:checked + label {
                color: $color-white;
                font-weight: bold;
                transition: background 0.3s ease;
            }

            input:checked + .pink {
                border-color: $color-pink;
                background-color: $color-pink;
            }

            input:checked + .yellow {
                border-color: $color-yellow;
                background-color: $color-yellow;
            }

            input:checked + .skyblue {
                border-color: $color-skyblue;
                background-color: $color-skyblue;
            }

            input:checked + .green {
                border-color: $color-green;
                background-color: $color-green;
            }

            &.participant-input-form {
                position: relative;
                flex-wrap: wrap;
                
                input {
                    flex-basis: calc(100% - 70px);
                    padding-right: 50px;
                }

                .add-participant-btn {
                    position: absolute;
                    right: 7px;
                    top: 8px;
                    width: 30px;
                    height: 30px;
                    border: 1px solid $color-border4;
                    border-radius: 50%;
                    background: url(../../../images/ico_add.svg) no-repeat center;
                    background-size: 10px;
                    text-indent: -9999px;
                }

                .participants-list {
                    display: flex;
                    align-items: center;
                    flex-wrap: wrap;
                    flex-basis: calc(100% -70px);
                    margin-top: 15px;
                    padding-left: 70px;

                    .participant-item {
                        padding-left: 10px;
                        margin-right: 10px;
                        margin-bottom: 10px;
                        border-radius: 5px;
                        border: 1px solid $color-border2;
                        overflow: hidden;

                        span {
                            font-size: 1.4rem;
                            line-height: 1.5;
                            color: $color-text2;
                        }

                        .delete-participant-btn {
                            width: 30px;
                            height: 30px;
                            margin-left: 10px;
                            border: none;
                            background: $color-white url(../../../images/ico_close.svg) no-repeat center;
                            background-size: 10px;
                            text-indent: -9999px;
                        }
                    }
                }
            }

            textarea {
                width: 100%;
                height: 135px;
                padding: 13px;
                border: none;
                border-radius: 7px;
                background-color: $color-bg-gray;
                resize: none;
                font-size: 1.4rem;
                font-family: 'Poppins', sans-serif;
                line-height: 1.5;
            }
        }
    }
}